import Depot from '../views/Depot';
import Market from '../views/Market';
import Loan from '../views/Loan';
import EU_03 from 'src/admin/EU_03';
import EU_06 from 'src/admin/EU_06';
import EU_08 from 'src/admin/EU_08';
import EU_11 from 'src/admin/EU_11';
import EU_12 from 'src/admin/EU_12';
export interface AppRoute {
    path: string;
    name: string;
    icon?: string;
    // tslint:disable-next-line no-any
    component?: any;
    upgrade?: boolean;
    redirect?: boolean;
    to?: string;
}

const appRoutes: AppRoute[] = [
    { path: '/depot', name: 'Depot', icon: 'pe-7s-wallet', component: Depot },
    { path: '/market', name: 'Market', icon: 'pe-7s-graph1', component: Market },
    { path: '/loan', name: 'Loans', icon: 'pe-7s-note2', component: Loan },
    { path: '/eu_03', name: 'EU_03', icon: 'pe-7s-note2', component: EU_03 },
    { path: '/eu_06', name: 'EU_06', icon: 'pe-7s-note2', component: EU_06 },
    { path: '/eu_08', name: 'EU_08', icon: 'pe-7s-note2', component: EU_08 },
    { path: '/eu_11', name: 'EU_11', icon: 'pe-7s-note2', component: EU_11 },
    { path: '/eu_12', name: 'EU_12', icon: 'pe-7s-note2', component: EU_12 },
    { path: '/', name: 'Depot', redirect: true, to: '/login' },
];

export default appRoutes;


