
import { StockType } from '../state/AppState';

export const getCorrectIconForType = (type: StockType) => {

    switch (type) {
        case 'HealthCare':
            return 'HealthCare';
        case 'Capital Goods/Heavy Machinery':
            return 'Capital Goods';
        case 'Finance':
            return 'Finance';
        case 'Insurance':
            return 'Insurance';
        case 'Information Technology':
            return 'Information Technology';
        case 'Data Processing Services/OTT Platforms':
            return 'Data Processing Services';
        case 'Hotels':
            return 'Hotels';
        case 'FMCG':
            return 'FMCG';
        case 'Oil and Gas':
            return 'Oil and Gas';
        case 'Renewable Energy':
            return 'Renewable Energy';
        default:
            return 'notFound';
    }
};


