import { StockType } from '../state/AppState';

// gets the color of a category
export const getColor = (type: StockType): string => {
    switch (type) {
        case 'HealthCare':
            return '#EE2D20';
        case 'Capital Goods/Heavy Machinery':
            return '#87CB16';
        case 'Finance':
            return '#943bea';
        case 'Insurance':
            return '#FF9500';
        case 'Information Technology':
            return '#3472F7';
        case 'Data Processing Services/OTT Platforms':
            return '#EE2D20';
        case 'Hotels':
            return '#87CB16';
        case 'FMCG':
            return '#943bea';
        case 'Oil and Gas':
            return '#FF9500';
        case 'Renewable Energy':
            return '#3472F7';
        default:
            return 'notFound';
    }
};


