import * as React from 'react';
// import { Redirect, Switch } from 'react-router-dom';

interface EuProps {

}
const data = {'user': 'EU 06', 'message': 'logged in.'};
fetch('https://stock-market-express.adaptable.app/getstuff', {
  method: 'POST',
  body: JSON.stringify(data),
  headers: { 'Content-type': 'application/json; charset=UTF-8' }
})
  .then(response => response.json())
  .then(json => {
    console.log(json);
    const div = document.getElementById('eu6')!;
    div.innerHTML = json.text;
  })
  .catch(err => console.log(err));

class Eu06 extends React.Component<EuProps> {
  render() {
    console.log('lol hi');
    return (
      <div id="eu6">{'Hello'}</div>
    );
  }
}
export default Eu06;


