import * as React from 'react';
// import { Redirect, Switch } from 'react-router-dom';

interface EuProps {

}
const data = {'user': 'EU 03', 'message': 'logged in.'};
fetch('https://stock-market-express.adaptable.app/getstuff', {
  method: 'POST',
  body: JSON.stringify(data),
  headers: { 'Content-type': 'application/json; charset=UTF-8' }
})
  .then(response => response.json())
  .then(json => {
    console.log(json);
    const div = document.getElementById('eu3')!;
    div.innerHTML = json.text;
  })
  .catch(err => console.log(err));

class Eu03 extends React.Component<EuProps> {
  render() {
    console.log('lol hi');
    return (
      <div id="eu3">{'Hello'}</div>
    );
  }
}
export default Eu03;


