import * as React from 'react';
import { AppState, Stock } from '../../state/AppState';
import { connect } from 'react-redux';
import { Col, Grid, Row } from 'react-bootstrap';
import { buyOrSellStock } from '../../state/stockMarket/stockMarketActions';
import StockmarketCard from './StockmarketCard';

interface MarketProps {
    stocks: Stock[];
    buy: (stock: string, amount: number, value: number) => void;
    sell: (stock: string, amount: number, value: number) => void;
}

interface MarketState {
}

class Market extends React.Component<MarketProps, MarketState> {

    constructor(props: MarketProps) {
        super(props);
    }

    render() {
        const { stocks, buy, sell } = this.props;

        return (
            <div className="content">
                <Grid fluid={true}>
                    <Row>
                        {
                            stocks.map((stock, index) => {
                                return (
                                    <Col key={stock.name} xs={12}>
                                        <StockmarketCard
                                            stock={stock}
                                            index={index}
                                            onBuy={(amount: number) => {
                                                buy(stock.name, amount, stock.value);
                                            }}
                                            onSell={(amount: number) => {
                                                sell(stock.name, amount, stock.value);
                                            }}
                                        />
                                    </Col>
                                );
                            }, )

                        }
                    </Row>
                </Grid>
            </div>
        );
    }
}

const mapStateToProps = (state: AppState) => ({
    stocks: state.stockMarket.stocks
});

// tslint:disable-next-line: no-any
const mapDispatchToProps = (dispatch: any) => ({
    buy: (stock: string, amount: number, value: number) => {
        if (Number.isInteger(amount)) {
            dispatch(buyOrSellStock(stock, amount));
            const data = { 'user': sessionStorage.getItem('username'), 'message': 'Bought ' + stock + 'stock for ' + value };
            fetch('https://stock-market-express.adaptable.app/writetofile', {
                method: 'POST',
                body: JSON.stringify(data),
                headers: { 'Content-type': 'application/json; charset=UTF-8' }
            })
                .then(response => response.json())
                .then(json => console.log(json))
                .catch(err => console.log(err));
        }
    },
    sell: (stock: string, amount: number, value: number) => {
        if (Number.isInteger(amount)) {
            dispatch(buyOrSellStock(stock, -amount));
            const data = { 'user': sessionStorage.getItem('username'), 'message': 'Sold ' + stock + ' stock for ' + value };
            fetch('https://stock-market-express.adaptable.app/writetofile', {
                method: 'POST',
                body: JSON.stringify(data),
                headers: { 'Content-type': 'application/json; charset=UTF-8' }
            })
                .then(response => response.json())
                .then(json => console.log(json))
                .catch(err => console.log(err));
        }
    },
});

export default connect(mapStateToProps, mapDispatchToProps)(Market);



